body {
  --header-height: 60px;
  --text-color: #000;
  --hover-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  width: 100%;
}

.container {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.logo {
  height: 40px;
  text-decoration: none;
}

.nav-list {
  justify-content: start;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-link {
  height: var(--header-height);
  align-content: center;
  align-items: center;
  margin-left: 20px;
  list-style: none;
  display: flex;
}

.nav-link:first-child {
  margin: 0;
}

.link {
  font-size: 12px;
  line-height: var(--header-height);
  text-transform: uppercase;
  color: var(--text-color);
  text-decoration: none;
}

.link:hover {
  color: var(--hover-color);
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

